const reducerManager = (state = initialState, action) => {
    // console.log(action)
    if(action.type === "ADD_HEADLINE"){
        return {
            ...state,
            headlines: state.headlines.concat(action.player),
            players: state.players.filter(p => p.id !== action.player.id)
        }
    }

    if ( action.type === "ADD_SUBSTITUTE" ){
        return {
            ...state,
            substitutes: state.substitutes.concat(action.player),
            players: state.players.filter( p => p.id !== action.player.id)
        }
    }

    if ( action.type === "REMOVE_HEADLINE"){
        return {
            ...state,
            headlines: state.headlines.filter(p => p.id !== action.player.id),
            players: state.players.concat(action.player)
        }
    }

    if ( action.type === "REMOVE_SUBSTITUTE"){
        return {
            ...state,
            substitutes: state.substitutes.filter(p => p.id !== action.player.id),
            players: state.players.concat(action.player)
        }
    }

    return state
} 

export default createStore(reducerManager)