import { connect } from "react-redux"
import court from "../court.svg"

const Headlines = ({headlines, removeHeadline}) => (
    <section>
        <h2>Headlines</h2>
        <div className="court">
            {
                headlines.map(p => (
                    <article className="headline" key={p.id}>
                        <div className="">
                            <img src={p.photo} alt={p.name} />
                            <button onClick={() => removeHeadline(p)}>X</button>
                        </div>
                        <p>{p.name}</p>
                    </article>
                ))
            }
            <img src={court} alt="Football Court" />
        </div>
    </section>
)

const mapStateToProps = state => ({
    headlines: state.headlines
})

const mapDispatchToProps = dispatch => ({
    removeHeadline(player){
        dispatch({
            type: "REMOVE_HEADLINE",
            player
        })
    }
})


export default connect(mapStateToProps, mapDispatchToProps) (Headlines)