import { connect } from "react-redux"

const Substitutes = ({substitutes, removeSubstitute}) => (
    <section>
        <h2>Substitutes</h2>
        <div className="substitutes">
            {
                substitutes.map(p => (
                    <article className="substitute" key={p.id}>
                        <div className="">
                            <img src={p.photo} alt={p.name} />
                            <button onClick={() => removeSubstitute(p)}>X</button>
                        </div>
                        <p>{p.name}</p>
                    </article>
                ))
            }
        </div>
    </section>
)

const mapStateToProps = state => ({
    substitutes: state.substitutes
})

const mapDispatchToProps = dispatch => ({
    removeSubstitute(player){
        dispatch({
            type: "REMOVE_SUBSTITUTE",
            player
        })
    }
})


export default connect(mapStateToProps, mapDispatchToProps) (Substitutes)